import { Component, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { appUrl } from 'src/app/app-url.module';
import { IUnit } from 'src/app/models/unit.model';
import { UnitService } from 'src/app/services/unit.service';

@Component({
  selector: 'app-unit',
  templateUrl: './unit.page.html',
  styleUrls: ['./unit.page.scss'],
})
export class UnitPage implements OnInit {
  url = appUrl;

  units: IUnit[] = [];

  constructor(private unitService: UnitService) {}

  ngOnInit() {
    this.onFetchData();
  }

  isRefresh(evt: any) {
    setTimeout(() => {
      this.onFetchData();
      evt.target.complete();
    }, 1000);
  }

  onFetchData() {
    this.unitService.getAllUnit().then((observe) => {
      observe.pipe(map((val) => val.unit)).subscribe({
        next: (data) => {
          this.units = data;
        },
        error: () => {},
        complete: () => {},
      });
    });
  }
}
